<template lang="pug">
button(type="button" :class="classes" @click="onClick" :style="style") 
  svg
    v-if="left_icon"
  | {{label}}
  svg
    v-if="right_icon"
</template>

<script>
export default {
  name: 'my-button',

  props: {
    label: {
      type: String,
      default: '',
    },
    left_icon: {
      type: String,
      default: '',
    },
    right_icon: {
      type: String,
      default: '',
    },
    theme: {
      type: String,
      default: 'filled',
      validator: function (value) {
        return ['filled', 'outline', 'flat', 'plain'].indexOf(value) !== -1;
      },
    },
    size: {
      type: String,
      default: 'medium',
      validator: function (value) {
        return ['small', 'medium', 'large'].indexOf(value) !== -1;
      },
    },
  },

  computed: {
    classes() {
      return {
        'storybook-button': true,
        [`storybook-button--${this.theme}`]: true,
        [`button-size--${this.size}`]: true,
      };
    },
    style() {
      return {
      };
    },
  },

  methods: {
    onClick() {
      this.$emit('onClick');
    },
  },
};
</script>

<style scoped lang="sass">

.storybook-button
  font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif
  font-weight: 700
  border: 0
  border-radius: 3em
  cursor: pointer
  display: inline-block
  line-height: 1

.storybook-button--primary
  color: white
  background-color: #1ea7fd

.storybook-button--secondary
  color: #333
  background-color: transparent
  box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px inset

.button-size--small
  font-size: 12px
  padding: 10px 16px

.button-size--medium
  font-size: 14px
  padding: 11px 20px

.button-size--large
  font-size: 16px
  padding: 12px 24px
</style>
